<template>
  <div v-if="logado === 'true' ? true : false">
    <p class="text-h6 font-weight-normal ma-5 ms-16 ps-16">
      Acesso R치pido
    </p>
    <v-container class="grey lighten-5">
      <grid-list :items="items" />
    </v-container>
    <v-container
      v-show="false"
      class="grey lighten-5 mt-5"
    >
      <p>
        Downloads:
      </p>
      <v-btn
        color="blue-grey"
        class="ma-2 white--text"
        href="./assets/manualusuario.pdf"
        download="Manual Sistema NVS"
      >
        Manual de Usu치rio
        <v-icon
          right
          dark
        >
          mdi-cloud-download
        </v-icon>
      </v-btn>
    </v-container>
  </div>
</template>

<script>
  import { get, sync } from 'vuex-pathify'

  export default {
    name: 'HomeView',

    components: {
      GridList: () => import('../layouts/default/GridList'),
    },

    data: () => ({
      logado: window.localStorage.logado,
    }),

    computed: {
      ...get('user', ['dark', 'gradient']),
      ...get('app', ['items', 'version']),
      ...sync('app', ['drawer', 'drawerImage', 'mini']),
    },

    mounted () {
      this.reloadOnce()
    },

    methods: {
      reloadOnce () {
        if (window.localStorage) {
          if (localStorage.getItem('reload')) {
            window.location.reload()
            localStorage.removeItem('reload')
          }
        }
      },
    },
  }
</script>
